/*Navigation bar*/

.navbar{
	margin-top: -1px;
	margin-bottom: 0px;
	margin-left: 0px;
	margin-right: 0px;
	border : 0px;
	border-radius: 0px;
	border-color: transparent;
	background: #000000;  /* fallback for old browsers */
	background: -webkit-linear-gradient(to left, #e74c3c, #000000);  /* Chrome 10-25, Safari 5.1-6 */
	background: -webkit-gradient(linear, right top, left top, from(#e74c3c), to(#000000));
	background: -webkit-linear-gradient(right, #e74c3c, #000000);
	background: -o-linear-gradient(right, #e74c3c, #000000);
	background: linear-gradient(to left, #e74c3c, #000000); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}




.brandname{
	font-size: 2.2em;
	padding-left: 0px;
}

.nav-link{
	color: white;
	font-weight: 500;
	font-size: 15px;	
	text-decoration: none;
}

.nav-link:hover{
	text-decoration: none;
	color: white;
}

/*Landing page*/

.landing-grid{
	text-align: center;
	padding: 0px;
	width: 100vw;
	overflow-x: hidden;
	overflow-y: hidden;
	background: #000000;  /* fallback for old browsers */
	background: -webkit-linear-gradient(to left, #e74c3c, #000000);  /* Chrome 10-25, Safari 5.1-6 */
	background: -webkit-gradient(linear, right top, left top, from(#e74c3c), to(#000000));
	background: -webkit-linear-gradient(right, #e74c3c, #000000);
	background: -o-linear-gradient(right, #e74c3c, #000000);
	background: linear-gradient(to left, #e74c3c, #000000); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */


}

.avatar-img{
	width: 230px;
	padding-top: 5.3em;
	padding-bottom: 3em;
}

.banner h1{
	color: white;
	font-size: 60px;
	font-weight: 100;
	font-family: 'Gugi', cursive;
}
.banner p{
	color: white;
	font-size: 20px;
	padding: 1em;
	font-family: 'Oxygen' , sans-serif;
}

.social-links{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	width: 50%;
	font-size: 50px;
	margin-left: 25%;	
}

.social-links a{
	color: white;
}

.social-links a:hover{
	-webkit-transform: rotate(360deg);
	    -ms-transform: rotate(360deg);
	        transform: rotate(360deg);
	-webkit-transition: 1s;
	-o-transition: 1s;
	transition: 1s;
}


/*Projects*/

.project-row{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
}

.card{
	width: 250px;
	padding-bottom: 0px;
	margin-top: 10px;
	-webkit-box-shadow: 0 5px 10px 0 rgba(0,0,0,0.2);
	        box-shadow: 0 5px 10px 0 rgba(0,0,0,0.2);
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
}

.card:hover {
    -webkit-box-shadow: 0 8px 16px 0 rgba(0,0,0,0.8);
            box-shadow: 0 8px 16px 0 rgba(0,0,0,0.8);
    -webkit-transition: 1s;
    -o-transition: 1s;
    transition: 1s;
}

.card-footer{
	padding: 2px 16px;
}

/*About Me*/

.about-grid{
	margin: auto;
	margin-top: 10%;
	-webkit-transition: 1s;
	-o-transition: 1s;
	transition: 1s;
}

.about-grid:hover{
	-webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
	        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    -webkit-transition: 1s;
    -o-transition: 1s;
    transition: 1s;
}


.text p{
	font-size: 1.5em;
	font-weight: 500;
	font-family: 'Alegreya Sans', sans-serif;
}



@media only screen and (max-width: 600px){
	.social-links a{
		padding-left: 10px;
	}

	.project-row{
		display: block;
		margin: auto; 
	}

	.card{
		margin-top: 10px;
		margin-bottom: 20px;
	}
}


